"use client";

import React, { forwardRef, useImperativeHandle } from 'react';
import { useMediaStream } from '../hooks/useMediaStream';

interface CameraPreviewProps {
  onTranscription: (text: string) => void;
}

export interface CameraPreviewRef {
  toggleCamera: () => void;
}

const CameraPreview = forwardRef<CameraPreviewRef, CameraPreviewProps>(
  ({ onTranscription }, ref) => {
    const { isVideoOn, videoRef, toggleCamera } = useMediaStream(onTranscription);

    useImperativeHandle(ref, () => ({
      toggleCamera
    }));

    if (!isVideoOn) return null;

    return (
      <div className="relative w-64 h-48 rounded-lg overflow-hidden">
        <video
          ref={videoRef}
          autoPlay
          playsInline
          muted
          className="w-full h-full object-cover"
        />
      </div>
    );
  }
);

CameraPreview.displayName = 'CameraPreview';

export default CameraPreview;
